(function ($) {
    $('.container-slider .slides').slick({

        arrows:true,
        slidesToShow: 2,
        slidesToScroll: 1,
        infinite: true,
        responsive: [
            {
                breakpoint: 991,
                settings: {
                    slidesToShow: 1,
                }
            },
            {
                breakpoint: 575,
                settings: {
                    slidesToShow: 1,
                    arrow: false
                }
            }
        ],
        prevArrow: '.btn-prev',
        nextArrow: '.btn-next'
    });

    $('.order form .container-checkbox label').on('click',function(e){
        if($(this).parent().hasClass('checked')){
            $(this).parent().removeClass('checked');
            $(this).find('input').attr('checked',false);
        }else{
            $(this).parent().addClass('checked');
            $(this).find('input').attr('checked',true);
        }
    });
    $('.order form').on('submit',function(e){
        var i=0;
        var n=3;
        $(this).find('.required').each(function(){
            if($(this).find('input').val()){
               i++
            }else{
                $(this).addClass('error');
            }
        });
        if(n!=i){
            return false;
        }
    });
    $('form input').on('focusin',function(){
        $(this).parent().removeClass('error');
    });

    $.fancybox.defaults.modal = true;

    $('.mobile-menu-link').fancybox();
    $('.btn-close').on('click', function(){
        $.fancybox.close();
    });


    $(".btn-header,.link-top").on("click", function (event) {
        event.preventDefault();
        var top = $(this.hash).offset().top ;
        $('body,html').animate({scrollTop: top}, 1000);

    });
    $(window).on('scroll', function(){
        if($(this).scrollTop() > $('header').outerHeight()){
            $('.link-top').addClass('active');
        }else{
            $('.link-top').removeClass('active');
        }
    })


}(jQuery));